@import url("https://fonts.googleapis.com/css?family=Montserrat:900");

@font-face {
  font-family: 'Lifehack';
  src: url("../fonts/lifehack_medium-webfont.woff2") format("woff2"), url("../fonts/lifehack_medium-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Gagalin';
  src: url("../fonts/gagalin-regular-webfont.woff2") format("woff2"), url("../fonts/gagalin-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Ubuntu';
  src: url("../fonts/ubuntu/ubuntu-b-webfont.woff2") format("woff2"), url("../fonts/ubuntu/ubuntu-b-webfont.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Ubuntu';
  src: url("../fonts/ubuntu/ubuntu-light-webfont.woff2") format("woff2"), url("../fonts/ubuntu/ubuntu-light-webfont.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Ubuntu';
  src: url("../fonts/ubuntu/ubuntu-medium-webfont.woff2") format("woff2"), url("../fonts/ubuntu/ubuntu-medium-webfont.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'Ubuntu';
  src: url("../fonts/ubuntu/ubuntu-regular-webfont.woff2") format("woff2"), url("../fonts/ubuntu/ubuntu-regular-webfont.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

.visuallyhidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

* {
  box-sizing: inherit;
}

*:before,
*:after {
  box-sizing: inherit;
}

body,
html {
  width: 100%;
  height: 100%;
}

html {
  box-sizing: border-box;
  font-size: 16px;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Ubuntu', Arial, Helvetica, sans-serif;
  line-height: 1;
  color: #2f3234;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
  margin: 0;
}

ul li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
  margin: 0;
}

p {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img,
embed,
object,
video {
  max-width: 100%;
}

.wrapper {
  width: 100%;
  height: 100%;
  position: relative;
  min-height: 100%;
  overflow: hidden;
}

.maincontent {
  height: 100%;
  min-height: 40.625rem;
  transition: 1s;
  position: relative;
}

.container {
  width: 95%;
  max-width: 58.75rem;
  margin: 0 auto;
  height: 100%;
}

.section {
  height: 100%;
  width: 100%;
  min-height: 40.625rem;
}

.title {
  font-family: Gagalin;
  font-size: 4.5rem;
  font-weight: 400;
}

.description {
  font-family: Lifehack;
  font-weight: 500;
}

.make {
  background-image: url("../img/bg/make-bg.jpg");
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: cover;
  background-color: rgba(0, 0, 0, 0.31);
}

.container_make {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 0.75rem 0 2.5625rem 0;
}

.header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo__link {
  display: block;
}

.logo {
  position: relative;
  z-index: 20;
  margin-right: auto;
}

.menu {
  margin-right: 0.9375rem;
}

.nav__list {
  display: flex;
}

.nav__item {
  display: flex;
  align-items: center;
  white-space: nowrap;
  text-transform: uppercase;
}

.nav__item:after {
  content: ' ';
  width: 5px;
  height: 5px;
  border-radius: 2.5px;
  background-color: #fff;
  margin: 0 0.75rem;
}

.nav__item:last-child:after {
  display: none;
}

.nav__link {
  color: #fff;
  font-size: 0.8125rem;
  font-weight: 500;
  font-family: Ubuntu;
}

.button_order {
  background-color: #e45028;
  width: 6.0625rem;
  height: 2.1875rem;
  color: #fff;
  font-size: 0.8125rem;
  font-weight: 500;
}

.button {
  border: none;
  border-radius: 5px;
  text-transform: uppercase;
  cursor: pointer;
}

.content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 0.8;
  margin-bottom: auto;
}

.content__logo {
  margin-right: 1.875rem;
}

.title_make_yellow {
  padding-top: 4.375rem;
  color: #f9b43b;
  line-height: 1;
  margin-bottom: 1.25rem;
}

.description_make_white {
  color: #ffffff;
  font-size: 4.0625rem;
  line-height: 1;
}

.arrow__img {
  fill: #edeeee;
  width: 1.875rem;
  height: 0.9375rem;
}

.hamburger {
  display: none;
  cursor: pointer;
}

.hamburger .hamburger__item:first-child {
  background-color: #f9b43b;
  height: 0.5rem;
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
}

.hamburger .hamburger__item:nth-child(2) {
  margin-bottom: 0.1875rem;
  background-color: saddlebrown;
}

.hamburger .hamburger__item:last-child {
  margin-bottom: 0;
  background-color: transparent;
  height: 0.75rem;
  font-size: 0.9375rem;
  transform: rotate(360deg);
  color: #fff;
}

.hamburger__item {
  width: 2.5rem;
  height: 0.3125rem;
  border-radius: 2px;
  background-color: #fff;
  margin-bottom: 0.5625rem;
  transition: .5s;
  font-size: 0;
}

.hamburger__item:last-child {
  margin-bottom: 0;
}

.arrow {
  cursor: pointer;
}

.sidebar {
  position: fixed;
  right: 5%;
  top: 10%;
  color: #fff;
  z-index: 10;
}

.outer-circle {
  width: 14px;
  height: 14px;
  border-radius: 7px;
  background-color: transparent;
  border: 1px solid transparent;
  position: relative;
  margin-bottom: 0.9375rem;
  transition: 0.4s;
}

.outer-circle:hover {
  border-color: #fff;
  cursor: pointer;
}

.inner-circle {
  width: 6px;
  height: 6px;
  border-radius: 3px;
  background-color: #fff;
  margin-bottom: 0.9375rem;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
}

.active-circle {
  border-color: #fff;
}

.best {
  background-image: url("../img/bg/best-bg.jpg");
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: cover;
}

.container_best {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.title_best_black {
  margin-bottom: 3.4375rem;
}

.worth__list {
  display: flex;
  justify-content: space-between;
}

.worth__item {
  width: 30%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.worth__logo {
  min-width: 6.25rem;
  min-height: 5.625rem;
  margin-bottom: 2.1875rem;
}

.title_worth {
  color: #2f3234;
  font-size: 1.49812rem;
  font-weight: 500;
  font-family: Lifehack;
  line-height: 1;
  text-align: center;
  margin-bottom: 1.25rem;
}

.description_worth {
  color: #2f3234;
  font-size: 0.875rem;
  font-weight: 300;
  text-align: center;
  line-height: 1.42;
}

.worth__img {
  width: 6.25rem;
  height: 5.625rem;
}

.worth__img_hand {
  fill: #f8b537;
}

.worth__img_meat {
  fill: #e34f23;
}

.worth__img_natural {
  fill: #8bc13c;
}

.team {
  background-image: url("../img/bg/team-bg.jpg");
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: cover;
  background-color: #c0afaa;
  position: relative;
}

.teammates {
  height: 100%;
  width: 50%;
  background-color: rgba(57, 59, 60, 0.9);
  position: absolute;
  top: 0;
  right: 0;
  padding-top: 7.0625rem;
}

.container-teammates {
  width: 64%;
  margin: 0 auto;
}

.title_teammates {
  color: #f9b43b;
  font-size: 4.48313rem;
  font-weight: 400;
  margin-bottom: 2.8125rem;
}

.teammates__item {
  margin-bottom: 2.1875rem;
}

.teammate__name {
  display: inline-flex;
  align-items: center;
  font-family: Gagalin;
  color: #fff;
  font-size: 0.87rem;
  font-weight: 400;
  margin-bottom: 1.0625rem;
}

.teammate__name:before {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border: 5px solid transparent;
  border-top-color: #f9b43b;
  border-bottom-width: 0px;
  margin-right: 0.625rem;
  transition: .8s;
}

.teammate__info {
  display: flex;
  align-items: flex-start;
}

.teammates__item_active .teammate__name {
  color: #f9b43b;
}

.teammates__item_active .teammate__name:before {
  transform: rotate(180deg);
}

.teammate__photo {
  display: inline-block;
  min-width: 6.25rem;
  margin: 0 1.25rem 0 1.25rem;
}

.teammate__img {
  height: 6.25rem;
  width: 6.25rem;
  border-radius: 50%;
}

.teammate__facts {
  color: #ffffff;
  font-size: 0.875rem;
}

.teammate__profession {
  font-weight: 700;
  margin-bottom: 1.25rem;
}

.teammate__description {
  font-weight: 300;
  line-height: 1.42;
}

.teammates__wrap {
  transition: .8s;
  height: 0;
  overflow: hidden;
}

.smenu {
  background-image: url("../img/bg/menu-bg.jpg");
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: cover;
  position: relative;
  width: 100%;
}

.title__smenu {
  position: absolute;
  top: 10%;
  left: 8.6%;
  color: #ffffff;
  font-size: 4.48313rem;
  font-weight: 400;
  z-index: 1;
}

.smenu__list {
  height: 100%;
  display: flex;
  justify-content: flex-end;
}

.smenu__item {
  display: flex;
  position: relative;
  z-index: 2;
}

.smenu__trigger {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 5rem;
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: cover;
  font-family: Gagalin;
  color: #ffffff;
  font-size: 2.4875rem;
  font-weight: 400;
  padding-bottom: 3.4375rem;
}

.smenu__title {
  transform: rotate(270deg);
  white-space: nowrap;
  width: 100%;
}

.smenu__trigger_meat {
  background-image: url("../img/content/acc-meat.jpg");
}

.smenu__trigger_veg {
  background-image: url("../img/content/acc-vegan.jpg");
}

.smenu__trigger_diet {
  background-image: url("../img/content/acc-dieta.jpg");
}

.smenu__content {
  transition: 1s;
  width: 0;
  overflow: hidden;
  background-color: rgba(57, 59, 60, 0.93);
}

.smenu__text {
  min-width: 33.75rem;
  color: white;
  line-height: 1.42;
  font-size: 0.875rem;
  padding: 5rem 1.875rem 0 1.875rem;
}

.smenu__item_active .smenu__title {
  color: #f9b43b;
}

.smenu__item_active .smenu__content {
  transition: 1s;
  width: 33.75rem;
}

.reviews {
  height: 100%;
  position: relative;
}

.reviews__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
}

.reviews__list {
  height: 100%;
}

.reviews__item {
  height: 50%;
  width: 25%;
}

.reviews__item:hover .review__content {
  z-index: 0;
}

.review {
  height: 100%;
  position: relative;
}

.review__img {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50%;
  position: absolute;
  filter: grayscale(100%);
  z-index: -1;
}

.review__img_one {
  background-image: url("../img/content/one.jpg");
}

.review__img_two {
  background-image: url("../img/content/two.jpg");
}

.review__img_three {
  background-image: url("../img/content/three.jpg");
}

.review__img_four {
  background-image: url("../img/content/four.jpg");
}

.review__img_five {
  background-image: url("../img/content/five.jpg");
}

.review__img_six {
  background-image: url("../img/content/six.jpg");
}

.review__img_seven {
  background-image: url("../img/content/seven.jpg");
}

.review__img_eight {
  background-image: url("../img/content/eight.jpg");
}

.review__content {
  position: absolute;
  z-index: -2;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1.25rem 1.25rem;
  background-color: rgba(79, 79, 79, 0.7);
}

.review__title {
  font-family: Gagalin;
  color: #ffffff;
  font-size: 0.87rem;
  font-weight: 400;
  align-self: flex-start;
  margin-bottom: 1.875rem;
}

.review__description {
  color: #ffffff;
  font-size: 0.875rem;
  font-weight: 300;
  line-height: 1.5rem;
  margin-bottom: 1.0625rem;
}

.review__button {
  align-self: flex-start;
}

.button_review {
  color: #ffffff;
  font-size: 0.8125rem;
  font-weight: 500;
  width: 6.875rem;
  height: 2.1875rem;
  background-color: #2f3234;
}

.button_mobile {
  color: #ffffff;
  font-size: 0.8125rem;
  font-weight: 500;
  background-color: #2f3234;
  display: none;
  padding: 0.625rem 1.25rem;
}

.delivery {
  background-image: url("../img/bg/delivery-bg.jpg");
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: cover;
}

.container_delivery {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.title_delivery {
  font-family: Gagalin;
  color: #f9b43b;
  font-size: 4.48313rem;
  font-weight: 400;
}

.form {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.fieldset {
  width: 49%;
  padding: 0;
  margin: 0;
  border: none;
}

.info {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.info__name,
.info__phone {
  width: 48%;
}

.addres {
  display: flex;
  justify-content: space-between;
}

.addres__house,
.addres__corps,
.addres__floor,
.addres__flat {
  width: 22%;
}

.right-field__name,
.left-field__name {
  font-family: Ubuntu;
  color: #ffffff;
  font-size: 0.875rem;
  font-weight: 300;
}

.left-field__input_center {
  text-align: center;
}

.left-field__input {
  width: 100%;
  height: 35px;
}

.right-field__text {
  width: 100%;
  height: 114px;
  padding: 0.625rem 0 0 0.625rem;
}

.left-field__input,
.right-field__text {
  border-radius: 5px;
  border-width: 1px;
  border-color: #d1cfcb;
  border-style: solid;
  color: #2f3234;
  font-size: 0.875rem;
  font-weight: 300;
}

.left-field__input:focus,
.right-field__text:focus {
  outline: 0;
  outline-offset: 0;
  border-width: 2px;
  border-color: #f9b43b;
  border-style: solid;
}

.left-field__name,
.right-field__name {
  margin-bottom: 0.625rem;
}

.title_delivery {
  margin-bottom: 2.8125rem;
}

.info,
.street {
  margin-bottom: 1.25rem;
}

.info .left-field__input,
.street .left-field__input {
  padding-left: 1.25rem;
}

.pay {
  display: flex;
}

.right-field__label_pay,
.right-field__label_recall {
  display: flex;
}

.right-field__radio,
.right-field__checkbox {
  margin-right: 0.625rem;
}

.pay__cash {
  margin-right: 1.25rem;
}

.comment {
  margin-bottom: 0.9375rem;
}

.pay {
  margin-bottom: 1.25rem;
}

.button_submit,
.button_clean {
  border-radius: 5px;
  background-color: #e45028;
  width: 6.0625rem;
  height: 2.1875rem;
  color: #ffffff;
  font-size: 0.8125rem;
  font-weight: 500;
  cursor: pointer;
}

.button_clean {
  background-color: #ffffff;
  color: #2f3234;
  margin-left: 0.625rem;
}

.check {
  margin-bottom: 1.25rem;
}

.right-field__label {
  position: relative;
}

.right-field__radio:checked + .radio-style > .radio-style__inner {
  border-radius: 50%;
  background-color: #e35028;
  width: 0.4375rem;
  height: 0.4375rem;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
}

.radio-style {
  margin-right: 0.625rem;
  border-radius: 50%;
  background-color: #ffffff;
  width: 0.9375rem;
  height: 0.9375rem;
  display: block;
  position: relative;
}

.right-field__name_check {
  padding-top: 0.125rem;
}

.right-field__checkbox:checked + .checkbox-style > .checkbox-style__inner {
  height: 0.9375rem;
  background-image: url("../img/content/check.png");
  background-repeat: no-repeat;
  background-position: 50%;
}

.checkbox-style {
  margin-right: 0.625rem;
  background-color: #ffffff;
  width: 0.9375rem;
  height: 0.9375rem;
  display: block;
  position: relative;
}

input::-webkit-input-placeholder {
  text-indent: 0px;
  transition: text-indent 0.5s ease;
}

input::-moz-placeholder {
  text-indent: 0px;
  transition: text-indent 0.5s ease;
}

input:-moz-placeholder {
  text-indent: 0px;
  transition: text-indent 0.5s ease;
}

input:-ms-input-placeholder {
  text-indent: 0px;
  transition: text-indent 0.5s ease;
}

input:focus::-webkit-input-placeholder {
  text-indent: 1000px;
  transition: text-indent 0.5s ease;
}

input:focus::-moz-placeholder {
  text-indent: 1000px;
  transition: text-indent 0.5s ease;
}

input:focus:-moz-placeholder {
  text-indent: 1000px;
  transition: text-indent 0.5s ease;
}

input:focus:-ms-input-placeholder {
  text-indent: 1000px;
  transition: text-indent 0.5s ease;
}

.map {
  background-color: #b0bec5;
  position: relative;
}

.footer {
  background-color: #090909;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 3.75rem;
}

.container_footer {
  display: flex;
}

.location,
.socials {
  width: 50%;
}

.location {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.socials {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.socials__item {
  margin-left: 1.25rem;
}

.socials__item:first-child {
  margin-left: 0;
}

.socials__link {
  display: flex;
}

.location__addres {
  color: #ffffff;
  font-size: 0.875rem;
  font-weight: 300;
  margin-bottom: 0.3125rem;
}

.location__icon {
  width: 0.75rem;
  height: 0.75rem;
  fill: #e45028;
  margin-right: 0.3125rem;
}

.location__link {
  color: #e45028;
  font-size: 14px;
  font-weight: 700;
}

.socials__list {
  display: flex;
}

.icon {
  width: 1.25rem;
  height: 1.25rem;
  fill: #5a5a5a;
  transition: .4s;
}

.icon:hover {
  fill: #fff;
}

.slider {
  display: flex;
  justify-content: center;
  align-items: center;
  background: -webkit-linear-gradient(90deg, #f9b43b 0%, #eb8c38 47%, #f9b43b 73%);
  background: -moz-linear-gradient(90deg, #f9b43b 0%, #eb8c38 47%, #f9b43b 73%);
  background: -o-linear-gradient(90deg, #f9b43b 0%, #eb8c38 47%, #f9b43b 73%);
  background: -ms-linear-gradient(90deg, #f9b43b 0%, #eb8c38 47%, #f9b43b 73%);
  background: linear-gradient(0deg, #f9b43b 0%, #eb8c38 47%, #f9b43b 73%);
}

.burgers {
  margin: 0 0.625rem;
  width: 58.75rem;
  overflow: hidden;
}

.burgers__list {
  position: relative;
  display: flex;
  transition: 2s;
}

.burgers__slide {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 58.75rem;
}

.left-arrow__icon {
  width: 1.6875rem;
  height: 0.9375rem;
  fill: #fff;
  transform: rotate(90deg);
