.reviews{
	height: 100%;
	position: relative;
}

.reviews__list {
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-start;
	align-items: flex-start;
}
.reviews__list {
	height: 100%;
}
.reviews__item {
	height: 50%;
	width: 25%;
	&:hover .review__content{
		z-index: 0;
	}
	@include tablets{
		height: 25%;
		width: 50%;
		text-align: center;
	}
}
.review {
	height: 100%;
	position: relative;
}
.review__img {
	width: 100%;
	height: 100%;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 50%;
	position: absolute;
	filter: grayscale(100%);
	z-index: -1;
}
@each $number in (one,two,three,four,five,six,seven,eight){
	.review__img_#{$number} {
		background-image: url('../img/content/#{$number}.jpg');
	}
}
.review__content {
	position: absolute;
	z-index: -2;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: rem(20px) rem(20px);
	background-color: rgba(79, 79, 79,.7);
	@include phones{
		width: 100%;
	}
}
.review__title {
	font-family: Gagalin;
	color:  #ffffff;
	font-size: rem(13.92px);
	font-weight: 400;
	align-self: flex-start;
	margin-bottom: rem(30px);
	@include tablets{
		align-self: center;
	}
	@include phones{
		font-size: rem(23.87px);
	}
}
.review__description {
	color:  #ffffff;
	font-size: rem(14px);
	font-weight: 300;
	line-height: rem(24px);
	margin-bottom: rem(17px);
	@include phones{
		display: none;
	}
}
.review__button {
	align-self: flex-start;
	@include tablets{
		align-self: center;
	}
}
.button_review{
	color:  #ffffff;
	font-size: rem(13px);
	font-weight: 500;
	width: rem(110px);
	height: rem(35px);
	background-color:  #2f3234;
	@include phones{
		display: none;
	}
}
.button_mobile{
	color:  #ffffff;
	font-size: rem(13px);
	font-weight: 500;
	background-color:  #2f3234;
	display: none;
	padding: rem(10px) rem(20px);
	@include phones{
		display: flex;
	}
}
